 <template>
  <div class="block">
    <span class="demonstration">
      <el-table :data="this.$store.getters.getStudentList" style="width: 100%">
        <el-table-column type="index" width="50"></el-table-column>
        <el-table-column prop="userIcon" label="用户头像" align="center" width="100">
          <template slot-scope="scope">
              <img :src="scope.row.userIcon" alt="" style="width: 36px;height:36px">
         </template>
        </el-table-column>
        <el-table-column prop="stuName" label="姓名" width="180"></el-table-column>
        <el-table-column prop="gender" label="性别" width="180"></el-table-column>
        <el-table-column prop="stuId" label="学号" width="180"></el-table-column>
        <el-table-column prop="className" label="班级"></el-table-column>
        <el-table-column prop="tellNum" label="电话号码"></el-table-column>
        <!-- 列表操作项 -->
        <el-table-column fixed="right" label="操作" width="100">
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
            <el-button @click="manageActivity(true,scope.row)" type="text" size="small">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>
    </span>
    <div style="position:absolute;bottom:20px;left:45%">
      <el-pagination
        :hide-on-single-page="true"
        layout="prev, pager, next"
        :current-page="Number($store.state.student.pageNum)"
        @current-change="getStudentList"
        :total="this.$store.state.student.data.total"
      ></el-pagination>
    </div>
    <!-- <el-dialog title="活动编辑" :visible.sync="mgeActVis" top="5vh">
      <el-form :model="activity" label-width="85px">
        <el-form-item label="活动名称">
          <el-input v-model="activity.activityName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="活动介绍">
          <el-input type="textarea" v-model="activity.activityInformation"></el-input>
        </el-form-item>
        <el-form-item label="最大人数">
          <el-input-number size="small" v-model="activity.activityPersonNum"></el-input-number>
        </el-form-item>
        <el-form-item label="开始时间">
          <el-date-picker
            type="datetime"
            placeholder="选择日期"
            v-model="activity.activityStart"
            style="width: 100%;"
            value-format="yyyy/MM/dd HH:mm:ss"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="结束时间">
          <el-date-picker
            type="datetime"
            placeholder="选择日期"
            v-model="activity.activityEnd"
            style="width: 100%;"
            value-format="yyyy/MM/dd HH:mm:ss"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="报名时间">
          <el-date-picker
            type="datetime"
            placeholder="选择日期"
            v-model="activity.activitySignUp"
            style="width: 100%;"
            value-format="yyyy/MM/dd HH:mm:ss"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="是否显示">
          <el-switch
            v-model="activity.activityShow"
            active-color="#13ce66"
            inactive-color="#ff4949"
          ></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submit('activity')">提交</el-button>
        <el-button @click="mgeActVis = false">取消</el-button>
        <el-button type="danger" @click="deleteActivity" v-show="edit">删除活动</el-button>
      </div>
    </el-dialog>-->
  </div>
</template>

  <script>
export default {
  inject: ["reload"],
  data() {
    return { mgeActVis: false, edit: false, activity: "" };
  },
  created() {
    this.getStudentList();
  },
  methods: {
    handleClick: function(a) {
      console.log(a);
    },
    getStudentList(pageNum = this.$store.state.student.pageNum) {
      //获得活动菜单
      if (this.$route.query.id)
        this.$api
          .getGroupAct({
            id: this.$route.query.id,
            page: { pageNum: pageNum, pageSize: 9, showAll: true }
          })
          .then(data => {
            this.$store.dispatch("pushActivity", {
              name: "activity",
              num: pageNum,
              data: data.data
            });
          });
      else
        this.$api
          .getAllUser({ pageNum: pageNum, pageSize: 9, showAll: true })
          .then(data => {
            this.$store.dispatch("pushStudent", {
              name: "student",
              num: pageNum,
              data: data.data
            });
          });
    },
    manageActivity: function(a, data) {
      //管理活动(true为修改,false为新建)
      this.mgeActVis = true;
      a
        ? (() => {
            this.activity = Object.assign({}, data);
            this.edit = true;
          })()
        : (() => {
            this.activity = Object.assign(
              {},
              this.$store.getters.getActivityPrototype
            );
            this.edit = false;
          })();
    },
    deleteActivity: function() {
      //删除活动
      this.$confirm("确定真的要删除该活动吗, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$api
            .delActivity(this.activity.activityId)
            .then(data => {
              let { success, errCode, errMsg } = data;
              if (success) {
                this.$message({
                  type: "success",
                  message: "删除成功!"
                });
                this.reload();
              } else {
                this.$message({
                  type: "error",
                  message: "删除失败，请重试!"
                });
              }
            })
            .catch(err => {
              console.log(err);
              this.$message({
                type: "error",
                message: "无法连接到服务器!"
              });
            });
        })
        .catch(() => {});
    }
  }
};
</script>


